<template>
  <v-responsive :class="bg">
    <v-container>
      <v-row class="text-center">
        <v-col class="mb-4">
          <h2 v-scrollanimation class="display-0 font-weight-bold mb-3">
            {{ $t(`menu.${title}`) | upper }}
          </h2>
          <v-img v-scrollanimation :src="require('@/assets/img/garisJudul.png')" class="ml-auto mr-auto" width="150px"></v-img>

        </v-col>

      </v-row>
    </v-container>
  </v-responsive>
</template>

<script>
export default {
  name: 'TitleVue',
  data: () => ({
  }),
  props: ['title', 'bg'],
  filters: {
    upper (value) {
      if (!value) return ''
      value = value.toString()
      return value.toUpperCase()
    }
  }
}
</script>
